import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { appRouterModule } from "./app.routes";
import 'rxjs/add/operator/map';

import { AppComponent } from './app.component';
import { QuizComponent } from './quiz/quiz.component';
import { DashboardComponent } from './admin/dashboard/dashboard.component';
import { AddQuestionComponent } from './admin/add-question/add-question.component';
import { ViewQuestionsComponent } from './admin/view-questions/view-questions.component';
import { LoginComponent } from './user/login/login.component';
import { RegisterComponent } from './user/register/register.component';
import { UserDashboardComponent } from './user/user-dashboard/user-dashboard.component';
import { HeaderComponent } from './user/header/header.component';
import { AdminHeaderComponent } from './admin/admin-header/admin-header.component';
import { ExamComponent } from './user/exam/exam.component';
import { TopScorerComponent } from './user/top-scorer/top-scorer.component';
import { AddQuizComponent } from './admin/add-quiz/add-quiz.component';
import { PublishQuizComponent } from './admin/publish-quiz/publish-quiz.component';

@NgModule({
  declarations: [
    AppComponent,
    QuizComponent,
	DashboardComponent,
    AddQuestionComponent,
    ViewQuestionsComponent,
    LoginComponent,
    RegisterComponent,
    UserDashboardComponent,
    HeaderComponent,
    AdminHeaderComponent,
    ExamComponent,
    TopScorerComponent,
    AddQuizComponent,
    PublishQuizComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
	appRouterModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



