import { Component, OnInit } from '@angular/core';
import 'rxjs/add/operator/map';
import {ViewQuestionsService} from './view-questions.service';


@Component({
  selector: 'app-view-questions',
  templateUrl: './view-questions.component.html',
  styleUrls: ['./view-questions.component.css'],
  providers: [ViewQuestionsService]
})
export class ViewQuestionsComponent implements OnInit {

info:string;
option1:string;
option2:string;
option3:string;
option4:string;
option5:string;
answer:string;
questions:Question[];


  constructor(private viewQuestionsService: ViewQuestionsService) {

    this.viewQuestionsService.viewQuestions().subscribe(response => {
    this.questions=response.responseObject.questions;
      console.log(response);
    });

   }

  ngOnInit() {
  }

}
interface Question{
  info:string;
  option1:string;
  option2:string;
  option3:string;
  option4:string;
  option5:string;
  answer:string;
}




